$background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
$color: #fff;
$color1: #868aaf;
$color2: #db3baa;
$color2-dark: darken($color2, 10%);
$color2-darker: darken($color2, 30%);
$background-image2: linear-gradient(to right, #ffecd2 0%, #fcb69f 100%);;
$background1: url('https://api.iconify.design/openmoji:radio-button.svg?color=currentColor') no-repeat center center / contain;

#player {
    height: 100vh;
    background: $background-image;
    overflow: hidden;
    .header {
      text-align: center;
      height: 110px;
      h1 {
        color: $color;
        font-size: 20px;
        padding-top: 20px;
      }
      p {
        color: $color1;
        font-size: 12px;
      }
      .balls {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
        span {
          display: block;
          width: 5px;
          height: 5px;
          border-radius: 50%;
          background: $color2-darker;
          margin: 0 4px;
  
          &.current {
            width: 8px;
            height: 8px;
            background: $color2;
          }
        }
        
      }
    }  
  
    .panels {
      width: 200vw;
      height: calc(100vh - 270px);
      display: flex;
      
      transition: transform 0.3s;
      align-items: center;
  
      &.panel1 {
        transform: translateX(0vw);
      }
      &.panel2 {
        transform: translateX(-100vw);
      }
      .panel-effect {
        width: 100vw;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .effect {
          position: relative;
          display: flex;
          justify-content: center;
          align-items: center;
          height: 40vh;
          >div {
            background-size: contain;
            position: absolute;
          }
          .effect-1 {
            background: url(../svg/effect-no-move.svg) 0 0 no-repeat;
            width: 70vw;
            height: 70vw;
            animation: rotate 20s linear infinite;
          }
          .effect-2 {
            background: url(../svg/effect-move1.svg) 0 0 no-repeat;
            width: 60vw;
            height: 60vw;
            animation: rotate 10s linear infinite reverse;
          }
          .effect-3 {
            background: url(../svg/effect-move2.svg) 0 0 no-repeat;
            width: 24vw;
            height: 24vw;
            animation: rotate 10s linear infinite;
          }
        }
        .lyric {
          text-align: center;
          p {
            font-size: 14px;
            color: $color1;
            margin-top: 8px;
  
            &.current {
              color: $color;
            }
          }
        }
      }
      .panel-lyrics {
        width: 100vw;
        height: calc(100% - 40px);
        text-align: center;
        overflow: hidden;
        .container {
          transition: all .4s;
          p {
            color: $color1;
            line-height: 2;
            font-size: 14px;
  
            &.current {
              color: $color;
            }
          }
        }
  
      }
    }
  
  
    .footer {
      height: 160px;
      .buttons {
        display: flex;
        justify-content: center;
    
        svg {
          width: 24px;
          height: 24px;
          margin: 0 20px;
        }
      }
    
      
      .bar-area {
        color: $color1;
        font-size: 12px;
        display: flex;
        padding: 0 20px;
        margin-top: 20px;
        align-items: center;
        .time-start {
          order: 1;
          width: 40px;
        }
        .time-end {
          order: 3;
          width: 40px;
        }
        .bar {
          order: 2;
          flex: 1;
          height: 4px;
          background: $background-image2;
          border-radius: 2px;
          margin: 0 10px;
          .progress {
            width: 0%;
            transition: all .2s;
            height: 100%;
            border-radius: 2px;
            background: $color2;
            position: relative;
    
            &::after {
              content: '';
              position: absolute;
              right: -8px;
              top: -7px;
              display: block;
              width: 16px;
              height: 16px;
              background: $background1;
              background-size: contain;
            }
          }
        }
      }
    
      .actions {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
        padding: 0 20px;
    
        svg {
          width: 28px;
          height: 28px;
    
          &.btn-play-pause {
            width: 50px;
            height: 50px;       
          }
        }
    
      }
    
    }
  
  }
  
  
  @keyframes rotate {
    0% {
      transform: rotate(0)
    }
    100% {
      transform: rotate(360deg)
    }
  }